import React, { useState } from "react";
import Navbar from "../Navbar/Navbar";

function Material() {

  const [material, setMaterial] = useState([

    { id: 1, img: 'Material/Mathematics.jpg', title: "Mathematics" },
    { id: 2, img: 'Material/Basic Algabra Operations.jpg', title: "Mathematic 2" },
    { id: 3, img: 'Material/problems and Solutions In Mathematics.jpg', title: "Mathematics 3" },
  ])
  return (
    <>
      <Navbar title={"Material"} />

      <div className="material">
        <div className="row mx-0 g-3">
          {material.map((item) =>

            <div key={item.id} className="col-md-6 col-lg-4">
              <div className="item">
                <figure className="m-0">
                  <img
                    src={require(`../../Assets/Images/${item.img}`)}
                    className="w-100"
                    alt=""
                  />
                </figure>
                <div className="title">
                  <h4>{item.title}</h4>
                  <i className="fa-solid fa-download"></i>
                </div>
              </div>
            </div>
          )}


        </div>
      </div>
    </>
  );
}

export default Material;
