import React, { useState } from "react";
import Navbar from "../Navbar/Navbar";

function Courses() {

  const [course, setCourse] = useState([

    { id: 1, img: 'chemistryCourses.jpg', title: "chemistry" },
    { id: 2, img: 'math.jpg', title: "Math" },
    { id: 3, img: 'physicsCources.jpg', title: "physics" }
  ])

  return (
    <>
      <Navbar title={"Courses"} />
      <div className="courses">
        <div className="row mx-0">
          <div className="row">
            <h3 className="section-title">
              All Courses <span>(6)</span>
            </h3>
          </div>
        </div>
        <div className="row mt-3  mx-0 g-3">

          {course.map((item) =>

            <div key={item.id} className="col-md-6 col-lg-4">
              <div className="item">
                <figure className="m-0">
                  <img
                    src={require(`../../Assets/Images/Courses/${item.img}`)}
                    className="w-100"
                    alt=""
                  />
                </figure>
                <div className="title">
                  <h4>{item.title} </h4>
                  <button>Open</button>
                </div>
              </div>
            </div>

          )}


        </div>
      </div>
    </>
  );
}

export default Courses;
