import React, { useState } from "react";
import Navbar from "../Navbar/Navbar";

function Library() {

  const [book, setBook] = useState([

    { id: 1, img: 'chemister1.jpg', title: "chemister1" },
    { id: 2, img: 'inorganic Chemistrty.jpg', title: "inorganic Chemistrty" },
    { id: 3, img: 'physics.jpg', title: "physics" }
  ])

  return (
    <>
      <Navbar title={"Library"} />

      <div className="library ">
        <div className="row">
          <h2 className="section-title ">Library</h2>
        </div>
        <div className="row mt-3 mx-0 g-3">
          {book.map((item) =>
            <div key={item.id} className="col-md-6 col-lg-4">
              <div className="item">
                <figure className="m-0">
                  <img
                    src={require(`../../Assets/Images/Library/${item.img}`)}
                    className="w-100"
                    alt=""
                  />
                </figure>
                <div className="title">
                  <h4>{item.title} </h4>
                  <i className="fa-solid fa-download"></i>
                </div>
              </div>
            </div>
          )}




        </div>
      </div>
    </>
  );
}

export default Library;
